const swiper = new Swiper('.swiper', {
    loop: true,
    speed: 500,
    autoplay: {
        delay: 3000,
        pauseOnMouseEnter: true,
    },
    pagination: {
        el: '.swiper-pagination',
        clickable: true,
        hideOnClick: false,
        bulletClass: 'pagination__bullet',
        bulletActiveClass: 'pagination__bullet_active',
    },
});
